@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
:root{
    --darkreader-text-gray: #dad7d2
}
*{
    font-family: 'Sora', sans-serif;
    box-sizing: border-box;
    margin:0;
    padding:0;
}
html{
    font-size: clamp(12px,.8rem + .25vw,16px);
}
body{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.button_nivel{
    text-decoration: none;
    color: #000;
    text-align: center;
    line-height: 60px;
    margin: auto;
    height: 60px;
    width: 200px;
    border-radius: 10px;
    border: none;
    font-size: 1.88rem;
}
.button_nivel{
    border: none;
    background-color: #19c3c9;
    background-image: linear-gradient(to right, #19c3c9 0%, #6cfbf1  50%, #1d9797  100%);
    background-size: 200% auto;
    cursor: pointer;
    transition: all 0.3s ease;
}
.button_nivel:hover{
    background-position: right center;
    -webkit-transform: scaleX(1.03);
            transform: scaleX(1.03);
}
@media screen and (max-width: 450px) {
    .button_nivel{
        height: 60px;
        max-width: 200px;
        width: 100%;
    }
}
:root{
  --fondo: #000;
}
.Lcontainer{
  flex-grow: 1;
  min-height: 80vh;
  width: 100%;
  background-color: #000;
  background-color: var(--fondo);
  place-content: center;
  display: grid;
  overflow: hidden;
}
.loader {
  font-size: 10px;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: linear-gradient(to right, #fff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
          animation: load3 1.4s infinite linear;
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #fff;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background-color: #000;
  background-color: var(--fondo);
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  inset: 0 0 0 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
